body{
    background: linear-gradient(90deg, #d53369 0%, #daae51 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: sans-serif;
  
  
  }
  .mainBody{
    height: 380px;
    background: white;
    width: 300px;
    box-shadow: 2px 2px 10px rgb(110, 101, 12);
    border: 0px;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  
  .info{
    
    
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 20px 0px;
    
  
  }
  .info h1{
    font-size: 2.5rem;
    color: rgb(53, 53, 53);
    margin:0px
    
    
    
  }
  .info h2{
    font-size: 1rem;
    margin: 10px 0px;
    color: grey;
    
  }
  .info h3{
    margin: 25px 0px;
    font-size: 1rem;
    color: grey;
    
  }